import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import EditUser from '@/components/EditUser'
import AddUser from '@/components/AddUser'
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/edit_user/:user_id',
      name: 'edit-user',
      component: EditUser
    },
    {
      path: '/add_user',
      name: 'add-user',
      component: AddUser
    },
  ]
})



