<template>
  <v-app-bar color="#26a69a" dark app>
    <v-app-bar-nav-icon></v-app-bar-nav-icon>
    <v-toolbar-title>
      <v-btn text @click="goHome" id="toolbar-title">
        <v-icon class="icon-padding-right">fa fa-house-user</v-icon>
        <h2>Rent App</h2>
      </v-btn>
    </v-toolbar-title>

    <v-spacer></v-spacer>

    <v-btn icon>
      <v-icon>fa fa-search</v-icon>
    </v-btn>

    <v-btn icon>
      <v-icon>fa fa-heart</v-icon>
    </v-btn>

    <v-btn icon>
      <v-icon>fa fa-ellipsis-v</v-icon>
    </v-btn>
  </v-app-bar>
</template>

<script>
export default {
  name: "navbar",
  methods: {
    goHome() {
      this.$router.push("/");
    }
  }
};
</script>

<style scoped>
.icon-padding-right {
  padding-right: 10px;
}
</style>


